* {
  color: #333;
  font-family: monospace;
}

#editor {
  margin: 0;
  padding: 0;
  box-shadow: 0 0 3px #33f;
}

#editor-frame {
  width: 360px;
  max-width: 100%;
  height: 360px;
  border: 0 solid #33f;
  margin: auto;
  position: relative;
}

#editor-frame canvas {
  width: 360px;
  max-width: 100%;
  image-rendering: crisp-edges;
  image-rendering: pixelated;
  position: absolute;
  inset: 0;
}

#tools {
  z-index: 5;
  -webkit-user-select: none;
  user-select: none;
  grid-row-gap: 8px;
  grid-template-rows: 50% 50%;
  grid-template-columns: 50% 50%;
  place-items: center;
  padding: 16px;
  display: grid;
  position: absolute;
  inset: 33%;
}

#tools img {
  filter: contrast(.01);
}

.selected {
  transform: scale(1.1);
  filter: none !important;
}

body {
  max-width: 900px;
  filter: drop-shadow(1px 1px 4px #6b7593);
  background-color: #faebd7;
  flex-direction: column;
  margin: auto;
  display: flex;
}

#render {
  image-rendering: crisp-edges;
  image-rendering: pixelated;
  place-self: flex-end end;
}

#target {
  width: 80%;
  max-width: 450px;
  justify-self: end;
  position: relative;
  overflow: hidden;
}

#target:after {
  content: attr(data-after);
  position: absolute;
  bottom: 4px;
  right: 4px;
}

@keyframes flash {
  0% {
    filter: drop-shadow(1px 1px #6b7593);
    transform: translateY(0);
  }

  10% {
    filter: drop-shadow(1px 1px 15px #6b7593);
    transform: translateY(-3px);
  }

  100% {
    filter: drop-shadow(1px 1px #6b7593);
    transform: translateY(0);
  }
}

.flash {
  animation: .4s ease-out flash;
}

#scale {
  image-rendering: pixelated;
  place-self: flex-end end;
}

textarea {
  height: 100%;
}

textarea, pre {
  width: 100%;
  background-color: #0000;
  margin: 0;
  overflow: hidden;
}

.broider {
  padding: 8px;
}

.showcase {
  width: 100%;
  grid-column-gap: 8px;
  grid-row-gap: 8px;
  grid-template-rows: 160px 160px auto;
  grid-template-columns: 33% auto 20%;
  margin: 8px auto auto;
  display: grid;
}

#color {
  box-sizing: content-box;
  height: 66px;
  width: 66px;
  background-color: #0000;
  border-width: 7px;
  border-image-width: 7px;
  place-self: flex-end start;
  padding: 0;
}

input[type="color"] {
  -webkit-appearance: none;
  width: 32px;
  height: 32px;
}

input[type="color"]::-webkit-color-swatch-wrapper {
  padding: 0;
}

input[type="color"]::-webkit-color-swatch {
  border: none;
}

@media only screen and (max-width: 700px) {
  .desktop {
    display: none !important;
  }

  .showcase {
    grid-template-rows: 160px;
    grid-template-columns: 33% auto 96px;
  }

  #render {
    position: fixed;
    right: 1000px;
  }

  .blurb {
    grid-area: 1 / 2 / 2 / 4 !important;
  }
}

.blurb {
  grid-area: 1 / 2 / 2 / 3;
}

@media only screen and (max-width: 600px) {
  * {
    font-size: 10px;
  }
}

